#feedback-js {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  flex-direction: column;
  box-sizing: border-box;
  display: flex;
  max-height: 100%;
  align-content: center;
  justify-content: center;
  align-items: center;
  font-family: Roboto, RobotoDraft, Helvetica, Arial, sans-serif; }
  #feedback-js div.fb-form-container {
