    #feedback-js div.fb-form-container form .fb-checkbox label {
      cursor: pointer;
      align-items: center;
      display: -webkit-flex;
      font-style: normal;
      font-variant: normal;
      font-weight: 400;
      font-stretch: normal;
      font-size: 14px;
      line-height: 20px;
      font-family: Roboto, RobotoDraft, Helvetica, Arial, sans-serif;
